@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&family=Paprika&family=Quicksand:wght@300;400;500;600;700&display=swap');

/* font-family: 'Dancing Script', cursive; */
/* font-family: 'Paprika', cursive; */
/* font-family: 'Quicksand', sans-serif; */

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Quicksand', sans-serif;
}
body{
    color: #1d1d1d;
}
a {
    color: unset;
    text-decoration: unset;
}

/* BACKGROUND */
#pageContainer {
    height: 100vh;
    width: 100%;
    background: #000000;
    background: -webkit-linear-gradient(to top, #434343, #000000);
    background: linear-gradient(to top, #434343, #000000);
    background-attachment: fixed;
    padding: 5vh 4vw;
}

/* CONTAINER */
#container {
    width: 100%;
    height: 100%;
    background-color: #fbfbfb;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.5);
    overflow-y: auto;
}

/* NAVBAR */
#navbar {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    height: 100px;
    padding: 10px 100px;
}
#logo {
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    align-items: center;
}
#logo .kalatea{
    font-family: 'Paprika', cursive;
    font-size: 32px;
    transition: color 150ms ease;
}
#logo:hover .kalatea{
    color: #106ea5;
}
#logo .clothes{
    font-family: 'Paprika', cursive;
    font-size: 20px;
    transition: color 150ms ease;
}
#logo:hover .clothes{
    color: #b11111;
}
#links {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    width: 700px;
}
#links button {
    color: #1d1d1d;
}
#cart {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    width: 130px;

    padding: 10px 15px;
    color: #fbfbfb;
    background-color: #1d1d1d;
}

/* HOME */
#homeContainer{
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    align-items: center;
}
#homeImg{
    width: 90%;
    height: 50vh;
    background-image: url('https://images5.alphacoders.com/492/thumb-1920-492030.jpg');
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: center;
    margin: 30px 0;
}
.homeProds{
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-around;
    align-items: center;
    width: 70%;
    margin: 20px 0;
}
.imgContainer{
    position: relative;
}
.homeProds .imgHomeProds{
    filter: grayscale(70%);
    display: block;
    width: 30vw;
    height: auto;
    box-shadow: 5px 5px 3px rgba(0, 0, 0, 0.7);
}
.overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: .5s ease;
    background-color: #1d1d1d;
}
.imgContainer:hover .overlay {
    opacity: 1;
}
.text {
    color: white;
    font-size: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
}
.imagenHome{
    transition: 150ms ease-in;
    width: 400px;
}
.imagenHome:hover {
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
}

/* PAGINAS */
.paginas{
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    align-items: center;
    margin: 50px 0;
}
.productos{
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
    margin: 40px 0;
    max-width: 1100px;
}
.producto{
    margin: 15px 0;
}
.detailProducto{
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 70vh;
}

#cardDetail{
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-around;
    align-items: center;
}
#cardDetail img{
    width: 400px;
    box-shadow: 5px 1px 4px rgba(0, 0, 0, 0.5);
}
#cardContentDetail{
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    height: 100%;
    text-align: center;
    max-width: 500px;
}
.boton{
    border: none;
    padding: 5px 10px;
    margin: 5px 0;
}
.botonCompra{
    border: none;
    padding: 5px 10px;
    margin: 5px 0;
}

#nike{
    width: 70%;
    height: 300px;
    background-image: url('https://i.pinimg.com/originals/41/e2/b6/41e2b604186c64d7604662d2234a2f99.jpg');
    background-repeat: no-repeat;
    background-size: 60%;
    background-position: center;
    border: 3px solid black;
    background-color: white;
}
#adidas{
    width: 70%;
    height: 300px;
    background-image: url('https://www.logaster.com/it/blog/wp-content/uploads/sites/13/2020/03/Adidas-Logo-old-1-1024x576-min-1024x576.jpg');
    background-repeat: no-repeat;
    background-size: 38%;
    background-position: center;
    border: 3px solid black;
    background-color: white;
}
#vans{
    width: 70%;
    height: 300px;
    background-image: url('https://www.newton.com.tw/img/1/0b8/cGcq5yNwUmYzQmNzU2Y1ETYldzYwQmMhZjNlRjY5QzM0MDOyEmM0QGZyQjMv0WZ0l2LjlGcvU2apFmYv02bj5SdklWYi5yYyN3Ztl2LvoDc0RHa.jpg');
    background-repeat: no-repeat;
    background-size: 50%;
    background-position: center;
    border: 3px solid black;
    background-color: #f6f6f6;
}

/* CONTADOR */
#contadorContainer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 50%;
    margin: 20px 0;
    border-radius: 20px;
    background-color: rgb(192, 192, 192);
}
#contadorContainer button{
    color: #fbfbfb;
    background-color: #1d1d1d;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;
    transition: 100ms ease-in-out;
}
#contadorContainer button.azul:hover{
    color: #ffffff;
    background-color: #106ea5;
}
#contadorContainer button.rojo:hover{
    color: #ffffff;
    background-color: #b11111;
}

#contador{
    font-size: 20px;
    font-weight: 500;
}

/* CARRITO */
#carritoVacio{
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-around;
    align-items: center;
    height: 80%;
}
#carritoVacio h1{
    font-family: 'Paprika', cursive;
}
#carritoVacio a{
    font-family: 'Paprika', cursive;
    font-size: 24px;
    color: #434343;
    transition: 150ms ease-in-out;
}
#carritoVacio a:hover{
    color: #c7bb1a;
}
#carritoVacio .link{
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-around;
    align-items: center;
    width: 50%;
}
#carrito{
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-around;
    align-items: center;
    margin: 30px 0;
}
#carrito table, .resumenTable{
    margin: 50px;
    text-align: center;
    width: 80%;
    border: 1px solid;
    padding: 10px;
    background-color: #ebebeb;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.5);
}
#carrito table img, .resumenTable img{
    width: 70px;
}
#borrar{
    color: rgb(151, 10, 10);
    cursor: pointer;
    transition: 100ms ease;
}
#borrar:hover{
    color: rgb(219, 55, 14);
}
.limpiar{
    border: none;
    padding: 10px 15px;
    margin: 15px 0;
    background-color: #1d1d1d;
    color: #fbfbfb;
    font-size: 14px;
    font-weight: 500;
    transition: 150ms ease;
    cursor: pointer;
}
.limpiarDisabled{
    border: none;
    padding: 10px 15px;
    margin: 15px 0;
    background-color: #333333;
    color: #bbbbbb;
    font-size: 14px;
    font-weight: 500;
    transition: 150ms ease;
    cursor: pointer;
}
.limpiar:hover{
    background-color: #333333;
    color: #ffffff;
}
#formularioContainer{
    width: 100%;
}
#formulario{
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}
.formularioInput{
    width: 60%;
}
#carritoPost{
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    align-items: center;
    margin: 30px 0;
}

/* RESUMEN */
#resumen{
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    align-items: center;
    margin: 50px 0;
}
#resumen form{
    margin: 25px 0;
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
    align-items: center;
}
#resumen input{
    width: 300px;
    text-align: center;
}